.adminMainbarContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-width: 937px;
  height: 100%;
  border-left: 1px solid var(--lightGrayOne);
}

.adminUserPageTitle {
  position: relative;
  top: 0x;
  font-size: 24px;
  font-weight: bold;
  line-height: 59px;
  margin: 8px 0 8px 20px;
}

.titleLine {
  position: absolute;
  top: 75px;
  left: 214px;
  width: 100vw;
  height: 1px;
  background-color: var(--lightGrayOne);
}

.userCardContainer {
  margin: 0px auto auto 16px;
  padding-top: 16px; 
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-flow: row;
  grid-gap: 16px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    display: none;
  }
}
